.menu {
  box-sizing: border-box;
    
  height: 100vh;
  background-color: #f9f9f9;
  
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 0;
    }
  }
    
  &__nav {
    margin-bottom: 48px;
  }
    
  &__phone__number {
    display: block;
    margin-bottom: 16px;
    
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
    
  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;
    
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
