.header {
  background-image: url(../img/header-bg-mobile.png);
  background-size: cover;
  background-position: center;
  
  
  &__content {
    
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }

  &__title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    color: #fff;

    @include onTablet {
      font-size: 48px;
    }
  }

  &__bottom {
    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 64px;
    }
  }
}
